<template>
  <div id="app" class="site">
    <header>
      <HeaderComponent/>
    </header>
    <div class="page">
      <h1 v-if="this.index<3">Form</h1>
      <p v-if="this.index<3">Below is a simple form, please fill out some of your details</p>
      <!--<img alt="Candide logo" src="./assets/Candide_1f3554_Full.svg">-->
      <PageOne v-if="this.index===1"/>
      <PageTwo v-if="this.index===2"/>
      <ConfirmationPage v-if="this.index===3"/>
      <div v-if="this.index<3" class="page__button-container" :class="this.index===1 ? 'page__button-container--end': ''">
        <button v-if="this.index>1" v-on:click="this.decreaseIndex">Back</button>
        <button v-on:click="this.increaseIndex">Next</button>
      </div>
      <div v-else class="page__button-container page__button-container--center">
        <button v-on:click="this.resetIndex">Reset</button>
      </div>
    </div>
    <footer>
      <FooterComponent/>
    </footer>
  </div>
</template>

<script>

import PageOne from './components/Page-One.vue'
import PageTwo from './components/Page-Two.vue'
import ConfirmationPage from './components/Confirmation-Page.vue'
import HeaderComponent from './components/Header.vue'
import FooterComponent from './components/Footer.vue'

export default {
  name: 'app',
  components: {
    HeaderComponent,
    PageOne,
    PageTwo,
    ConfirmationPage,
    FooterComponent
  },
  data() {
    return {
      index: 1
    }
  },
  mounted() {
    this.$on('next-page', this.increaseIndex);
  },
  methods: {
    increaseIndex: function(){
      if(this.index<3){
        this.index++;
      }
    },
    decreaseIndex: function(){
      if(this.index>1){
        this.index--;
      }
    },
    resetIndex: function(){
      this.index = 1;
    },
    switchColours: function(){

    }
  },
}
</script>

